import React from 'react';
import {Panel, Div, Button, Cell, Avatar, ModalRoot, ModalPage, ModalPageHeader, HeaderButton, IS_PLATFORM_IOS, IS_PLATFORM_ANDROID, ModalCard, FixedLayout} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import VKStories from "vk-stories";
import connect from "@vkontakte/vkui-connect-promise";
import Icon24Cancel from '@vkontakte/icons/dist/24/cancel';
import Icon24Done from '@vkontakte/icons/dist/24/done';
VKStories.init(connect);




//компонент модального окна
class Modal extends React.Component {
  constructor(props) {
      super(props);
  }



  

  GenerateStory = () => {
      VKStories.generateStoryFromTemplate(require("../img/Design-story.png"), [
      {
        font: "50px Arial",
        align: "center",
        color: "#FFFFFF",
        value: this.props.selectedPlace.name,
        x: 540,
        y: 1133,
        }
     ], ("https://img-fotki.yandex.ru/get/517076/98038630.148/0_c2dd3_8ed48b43_L") )
     .then( (story) => {
        VKStories.shareStory(7150376, story, { add_to_news: true })
        
    })
}


con = () => {
  console.log(this.props.selectedPlace.img);
}

  render() {
    return(
    <ModalRoot activeModal={'story'}>
      <ModalCard
            id={'story'}
            onClose={() => this.setActiveModal(null)}
            icon={<Avatar src={this.props.selectedPlace.img} size={72} />}
            title="Поделиться местом с друзьми"
            caption="Пусть друзья узнают, куда вы добрались!!!"
            style={{}}
            onClose={()=>{this.props.cancelHistory()}}
            actions={[{
              title: 'Поделиться',
              type: 'primary',
              action: () => {
                this.props.sendHistory();
                this.GenerateStory();
                this.con();
              }
            }
            ]}
          />
        </ModalRoot>

   );
  }

}

export default Modal;
